<template>
  <div>
    <!-- ÏôÑÎ£å(Ïò§ÎäòÎÇ†Ïßú ÏßÄÎÇúÍ≤É) / ÎØ∏ÏôÑÎ£å ÌïÑÌÑ∞ÎßÅ -->
    <div class="d-flex justify-content-start align-items-center mb-5" style="margin-left: 5%;">
      <div class="form-check p-3 lg2 mr-3">
        <input class="form-check-input" type="radio" name="targetList" id="finished" value="finished" v-model="sortBy" />
        <label class="form-check-label" for="finished">
          ÏôÑÎ£å
        </label>
      </div>
      <div class="form-check p-3 lg2">
        <input class="form-check-input" type="radio" name="targetList" id="notFinished" value="not-finished" v-model="sortBy" >
        <label class="form-check-label" for="notFinished">
          ÏßÑÌñâÏ§ë
        </label>
      </div>
    </div>

    <!-- Î™©ÌëúÍ∞Ä ÏóÜÎäî Í≤ΩÏö∞ Î©îÏãúÏßÄ ÌëúÏãú -->
    <div v-if="filteredMileages.length === 0" class="text-center mb-4" style="color: gray;">
      ÏßÑÌñâ Ï§ëÏù∏ Î™©ÌëúÍ∞Ä ÏóÜÏäµÎãàÎã§.
    </div>

    <div class="row">
      <div v-for="(targets, index) in filteredMileages" :key="targets.mile_no" class="col-md-4 mb-3">
        <div class="p-3">
          <div style="text-align: center; font-family: KB_C2; font-size: 16pt;" class="mb-2">{{ targets.mile_name }} ÎßàÏùºÎ¶¨ÏßÄ</div>
          <div :style="{backgroundColor : backgroundColors[index % backgroundColors.length]}" style="width: 250px; height:300px; transition: transform 0.3s ease;" class="mx-auto rounded-4 target-box">
            <!-- Ïó¨Í∏∞ ÌïòÎìúÏΩîÎî© Ìïú Î∂ÄÎ∂Ñ Îç∞Ïù¥ÌÑ∞ Î∂àÎü¨Ïò§Í∏∞!! -->
            <div class="py-4">
              <span class="lg2" style="font-family: 'KB_C1';">Î™©ÌëúÍ∏∞Í∞Ñ</span><br>
              <span class="md">{{ targets.start_date }} - {{ targets.end_date }}</span>
            </div>
            <div class="py-3">
              <span class="lg2" style="font-family: 'KB_C1';">Î™©Ìëú ÎßàÏùºÎ¶¨ÏßÄ</span><br>
              <span class="lg2">{{targets.target_mileage}}</span>
            </div>
            <div class="py-3">
              <span class="bold-x-lg" style="font-family: 'KB_C1';">{{ targets.achievementRate }} / {{targets.target_mileage }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div class="d-flex justify-content-center" style="margin-top: 12vh;">
    <button
      class="btn-green mt-2 KB_C2"
      @click="openModal"
      style="
        width:8vw; height: 3vw; font-size:1.2vw; font-family: KB_C2;
      "
    >
      Ï∂îÍ∞ÄÌïòÍ∏∞
    </button>
  </div>

  <!-- Î™®Îã¨ -->
  <div v-if="isModalOpen" class="modal-overlay">
    <div class="modal-content">
      <div class="modal-header">
        <h2 class="modal-title">Í∞úÏù∏Î≥Ñ Î™©Ìëú ÏÑ§Ï†ïÌïòÍ∏∞</h2>
        <button class="close-button" @click="closeModal">&times;</button>
      </div>
      <div class="modal-body">
        <div class="form-group">
          <label for="mileage-select">Î™©Ìëú ÎßàÏùºÎ¶¨ÏßÄ</label>
          <div class="select-wrapper">
            <select  v-model="selectedLabel" id="mileage-select" class="form-select">
              <option v-for="label in labels" :key="label" :value="label">{{ label.mile_name }}</option>
            </select>
            <i class="bi bi-caret-down-fill select-icon"></i>
          </div>
        </div>
        <div class="form-group">
          <label>Î™©Ìëú Í∏∞Í∞Ñ</label>
          <div class="date-range">
            <Datepicker v-model="startDate" :format="formatDate" placeholder="ÏãúÏûëÏùº" />
            <span class="date-separator">~</span>
            <Datepicker v-model="endDate" :format="formatDate" placeholder="Ï¢ÖÎ£åÏùº" />
          </div>
        </div>
        <div class="form-group">
          <label for="mileage-score">Î™©Ìëú ÎßàÏùºÎ¶¨ÏßÄ Ï†êÏàò</label>
          <input type="number" id="mileage-score" v-model="targetScore" />
        </div>
      </div>
      <div class="modal-footer">
        <button class="submit-button" @click="addAction">Îì±Î°ù</button>
      </div>
    </div>
  </div>
</template>

<script>
import {mapState, mapGetters, mapActions } from "vuex";
import api from '@/api/axios';
import Datepicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css';

export default {

//====================================================================
  name: "PersonalTarget", //Ïª¥Ìè¨ÎÑåÌä∏Ïùò Ïù¥Î¶Ñ
  emits: ['dataLoaded'], // Ïù¥ Ïù¥Î≤§Ìä∏Î•º Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú Ï≤òÎ¶¨Ìï®ÏùÑ ÏÑ†Ïñ∏
//====================================================================
  components: { 
    Datepicker, //Ïù¥ Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä ÏÇ¨Ïö©Ìï† ÌïòÏúÑ Ïª¥Ìè¨ÎÑåÌä∏
  },

//====================================================================
  data() { //Ïª¥Ìè¨ÎÑåÌä∏Ïùò Î∞òÏùëÌòï ÏÉÅÌÉúÎ•º Ï†ïÏùò,Ïª¥Ìè¨ÎÑåÌä∏Ïùò Î©îÏÑúÎìúÎÇò Í≥ÑÏÇ∞Îêú ÏÜçÏÑ±ÏóêÏÑú Ï∞∏Ï°∞
    return {
      backgroundColors: ["#F4FBF2", "#F3FBFE", "#F5F5FF", "#FFF2FD", "#F4F4F4", "#FDF5F5", "#FBF4EE", "#FCFBF1"],
      // mileages: [], // Ï¥àÍ∏∞ Îç∞Ïù¥ÌÑ∞ Î∞∞Ïó¥ 
      isModalOpen: false,
      labels:[],
      selectedLabel: null,
      startDate: null,
      endDate: null,
      targetScore: 0,
      sortBy: "not-finished", // ÎØ∏ÏôÑÎ£å ÌïÑÌÑ∞ÎßÅ Í∏∞Î≥∏ Í∞í
    };
  },

//===================================================================
  methods: {  //Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú ÏàòÌñâÌï† Ìï®Ïàò
    ...mapActions('target',['addTarget','fetchTargets']),

    loginInfo() {
      return this.getLoginInfo;
    },
    isChecked() {
      return this.getIsChecked;
    },
    isLoggedIn() {
      return !!this.loginInfo;
    },
    async fetchMileages() {  // ÎßàÏùºÎ¶¨ÏßÄ Ïπ¥ÌÖåÍ≥†Î¶¨ Í∞ÄÏ†∏Ïò§Í∏∞
      try {
          const response = await api.get(
          'http://localhost:8090/notice/mileage'
          );
          console.log('fetchMileages Î©îÏÜåÎìú :', response.data); // ÏùëÎãµ Îç∞Ïù¥ÌÑ∞Î•º ÏΩòÏÜîÏóê Ï∂úÎ†•
          this.labels = response.data;
      } catch (error) {
          console.error(
          'Error fetching mileages:',
          error.response ? error.response.data : error.message
          );
      }
    },
    handleCheckboxChange(sortType) {
      if (sortType === 'finished' && this.sortByFin) {
        this.sortByNotFin = false;
      } else if (sortType === 'not-finished' && this.sortByNotFin) {
        this.sortByFin = false;
      }
    },
    formatDate(date) {
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0');
      const day = String(date.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },
    openModal() {
      this.isModalOpen = true;
    },
    closeModal() {
      this.isModalOpen = false;
    },
    async addAction() {
      const target = {
        mileNo: this.selectedLabel.mile_no,
        userNo: this.loginInfo.user_no, // Ïú†Ï†Ä ÏïÑÏù¥ÎîîÎ•º Ï†ÅÏ†àÌûà Î≥ÄÍ≤Ω
        startDate: this.startDate,
        endDate: this.endDate,
        targetMileage: this.targetScore,
        isTogether: false,
        isManagerPlan: false,
        month: new Date().getMonth() + 1, //1~12Ïõî Î≤îÏúÑÎ•º ÎßûÏ∂îÍ∏∞ ÏúÑÌï¥ÏÑú
        achievementRate: 0 //Î™©Ìëú Îã¨ÏÑ±Î•†
      };
      console.log('targetÏù¥ ÏÑúÎ≤ÑÎ°ú Îç∞Ïù¥ÌÑ∞ ÎÑòÍ∏∞Îäî Í≤É :',target);
      try {
        await this.addTarget(target);
        this.closeModal();
      } catch (error) {
        console.error('Error adding target:', error.response ? error.response.data : error.message);
      }
    },
  },
    
//====================================================================
  created() { //Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä ÏÉùÏÑ±Îê† Îïå Ïã§ÌñâÎêòÎäî ÌõÖ
    console.log('targer loginInfo Ïù¥ Ï∞çÌûàÎÇòÏöî ?:', this.loginInfo);
    console.log('isLoggedIn:', this.isLoggedIn);
    console.log('isChecked:', this.isChecked);
    // const userNo = this.loginInfo.user_no;
    this.fetchMileages();
    this.fetchTargets(this.loginInfo.user_no).then(() => {
    console.log('Targets after fetch:', this.getTargets); 
  });
  },

//=====================================================================
  computed: { //Í∞êÏãúÏûê Îç∞Ïù¥ÌÑ∞Í∞Ä Î≥ÄÍ≤ΩÎê† Îïå ÏûêÎèôÏúºÎ°ú Îã§Ïãú Í≥ÑÏÇ∞
    ...mapGetters('login', ['getLoginInfo', 'getIsChecked']),
    ...mapState('login', ['loginInfo']),
    ...mapGetters('target',['getTargets']),

    targets(){
      const targets = this.getTargets;
      console.log('Targets:', targets);
      return Array.isArray(targets) ? targets : []; // Î∞∞Ïó¥Ïù¥ ÏïÑÎãàÎ©¥ Îπà Î∞∞Ïó¥ÏùÑ Î∞òÌôò
    },

    filteredMileages() {
    const currentDate = new Date();
    return this.targets.filter((mileage) => {
      if (!mileage || !mileage.end_date) { // end_dateÍ∞Ä ÏóÜÏúºÎ©¥ Í≤ΩÍ≥† Î©îÏãúÏßÄÎ•º Ï∂úÎ†•
        console.warn('Skipping mileage due to missing endDate:', mileage);
        return false;
      }

      // end_dateÎ•º Date Í∞ùÏ≤¥Î°ú Î≥ÄÌôò
      const endDate = new Date(mileage.end_date);

      if (isNaN(endDate.getTime())) {
        console.warn('Invalid date format in endDate:', mileage.end_date);
        return false; // ÎÇ†Ïßú ÌòïÏãùÏù¥ Ïú†Ìö®ÌïòÏßÄ ÏïäÏúºÎ©¥ Ï†úÏô∏
      }

      if (this.sortBy === 'finished') {
        return endDate < currentDate;
      } else if (this.sortBy === 'not-finished') {
        return endDate >= currentDate;
      } else {
        return true;
      }
    });
  },
},

};
</script>











